<template>
	<div id="TabBarItem" @click="itemClick">
    <div v-show="!isActive"><slot name="item-icon"></slot></div>
    <div v-show="isActive"><slot name="item-icon-active"></slot></div>
<!--    <div :class="{'activeClass': isActive}"><slot name="item-text"></slot></div>-->
    <div :style="activeStyle"><slot name="item-text"></slot></div>
  </div>
</template>

<script>
  export default {
    name: "TabBarItem",
    props: {
      path: String,
      activeColor: {
        type: String,
        default: 'red'
      }
    },
    data() {
      return {
      }
    },
    computed: {
      isActive: function() {
        return this.path === this.$route.path;
      },
      activeStyle: function() {
        // this.isActive ? return {color: this.activeColor} : return {} // 错误写法
        return this.isActive ? {color: this.activeColor} : {}
      }
    },
    methods: {
      itemClick() {
        this.$router.push({path: this.path});
      }
    }
  }
</script>

<style scoped>
  #TabBarItem {
    flex: 1;
    height: 49px;
    text-align: center;
    font-size: 14px;
  }
  #TabBarItem img {
    width: 24px;
    height: 24px;
    margin-top: 3px;
    /*vertical-align: center;*/ /*错误写法，根本没有center这个值*/
    vertical-align: middle;
    margin-bottom: 2px;
  }
  .activeClass {
    color: deeppink;
  }

</style>
