import Vue from 'vue'
import VueRouter from 'vue-router'
import pages from './page'

const Main = () => import('../views/main/index')
const Home = () => import('../views/home/Home')
const Category = () => import('../views/category/Category')
const Cart = () => import('../views/cart/Cart')
const Profile = () => import('../views/profile/Profile')
const Draft = () => import('../views/draft/Draft')

// 1.安装插件
Vue.use(VueRouter)

// 2.创建router
const routes = [
  {
    path: '',
    redirect: '/main'
    // redirect: '/home'
  },
  {
    path: '/main',
    component: Main
  },
  {
    path: '/draft',
    component: Draft,
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/category',
    component: Category
  },
  {
    path: '/cart',
    component: Cart
  },
  {
    path: '/profile',
    component: Profile
  },
  {
    path: '/seedstore',
    component: () => import('views/seedstore/SeedStore')
  },
  {
    path: '/test',
    // component: () => import('views/seedstore/test')
    // component: () => import('views/seedstore/childConps/StoreCard')
    component: () => import('views/seedstore/childConps/OptionList')
  },
  {
    path: '/test2',
      component: () => import('views/seedstore/test/test')
      // component: () => import('views/seedstore/childConps/StoreCard')
      // component: () => import('views/seedstore/childConps/OptionList')
  },
  {
    path: '/test3',
    component: () => import('views/seedstore/test/test3')
  },
  {
    path: '/test4',
    component: () => import('views/seedstore/test/test4')
  },
  {
    path: '/test5',
    component: () => import('views/seedstore/test/test5')
  },
  {
    path: '/lab/multistage',
    component: () => import('views/lab/multistage/index')
  }
]

routes.push(...pages)

const router = new VueRouter({
  routes,
  mode: 'hash'
  // mode: 'history'
})


export default router
